.header-title {
    text-align: left;
    display: flex;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 50px;

    & > span {
        font-size: 30px;
      font-weight: 900;
      padding: 0 10px;
    }

    & > h1 {
      line-height: 50px;
      border-bottom: 1px gray solid;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        width: 54px;
        height: 2px;
        background: #151d2a;
        bottom: -1px;
        right: 0;
      }
    }

    & >p {
        font-size: 20px;
        font-weight: 500;
    }
  }
