.homeContainer {
  height: 905px;
  background-image: url("../../img/4166342.jpg");
  background-size: cover;
  overflow: hidden;

  &>.container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    flex-direction: column;
    position: relative;

    &::before {
      content: "";
      display: table;
    }

    &::after {
      content: "";
      display: table;
      clear: both;
    }

    &>.big-title {
      font-size: 50px;

      &>span {
        position: relative;
        display: inline-block;

        &::before {
          content: "";
          position: absolute;
          background: black;
          height: 115%;
          width: 4px;
          top: 15px;
          left: -8%;
        }

        &::after {
          content: "";
          position: absolute;
          background: black;
          height: 115%;
          width: 4px;
          top: 15px;
          right: -8%;
        }

        &>.sub-span {
          position: absolute;
          top: 116%;
          left: -8%;
          width: 116%;
          overflow: hidden;
          height: 100%;

          &>span {
            font-size: 18px;
            position: relative;
            top: -50%;

            @media only screen and (max-width: 600px) {
              top: 0%;
            }

            &:before,
            &:after {
              background-color: #000;
              content: "";
              display: inline-block;
              height: 4px;
              position: relative;
              vertical-align: middle;
              width: 50%;
            }

            &:before {
              right: 0.5em;
              margin-left: -50%;
            }

            &:after {
              left: 0.5em;
              margin-right: -50%;
            }
          }
        }
      }
    }

    &>.learn-more {
      position: absolute;
      bottom: 0;

      &>a {
        text-decoration: none;
        font-weight: bold;
        color: black;

        &:hover {
          opacity: 0.5;
        }

        &>.icon {
          margin-top: 20px;
          margin-bottom: 20px;
          animation: moveDown 1.3s cubic-bezier(0.07, 0.5, 0.58, 1) infinite;
        }
      }
    }

    &>.download {
      margin-top: 100px;
      font-size: 20px;
      cursor: pointer;

      &>.wrapper {
        display: block;
        position: relative;
        overflow: hidden;
        padding: 20px 40px;
        border: 1px solid black;
        text-decoration: none;
        color: black;

        &>.slide {
          position: absolute;
          color: white;
          right: -250px;
          padding: 20px 40px;
          background-color: black;
          transition: 0.5s;
          display: inline;
          top: 0;
        }

        &:hover .slide {
          right: 0;
        }
      }


    }
  }
}

@keyframes moveDown {
  0% {
    transform: translateY(10px);
  }

  50% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(10px);
  }
}