.resume {
    padding: 0 375px;
    @media only screen and (max-width: 1475px) {
        padding: 0 200px;
      }
    
      @media only screen and (max-width: 958px) {
        padding: 0 20px;
      }


  &_children {
    text-align: left;
  }

  &_detail {
    display: list-item;

    & > h4 {
        display: inline-block;
    }
  }
}
