.skills {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 50px;
  &_title {
    font-size: 28px;
    color: white;
    text-transform: uppercase;
    position: relative;


    &::before {
      position: absolute;
      content: "";
      width: 36px;
      height: 1px;
      background: #fff;
      bottom: -16px;
      display: inline-block;
      left: 50%;
      margin-left: -18px;
    }
  }

  &_info {
    margin-top: 20px;
    color: white;
    display: flex;
    width: 80%;
    justify-content: space-between;

    & > div {
      width: 120px;
    }

    &-item {
      & > span {
        &:first-child {
          font-size: 40px;
          font-weight: bold;
        }
        display: block;
      }
    }
  }
}
