.statistics {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 50px;
  &_title {
    font-size: 32px;
    color: white;
    text-transform: uppercase;
    position: relative;


    &::before {
      position: absolute;
      content: "";
      width: 36px;
      height: 1px;
      background: #fff;
      bottom: -16px;
      display: inline-block;
      left: 50%;
      margin-left: -18px;
    }
  }

  &_ava {
    width: 20vw;
    height: 20vw;
    padding: 20px;
    border: 5px solid red;
    border-radius: 50%;
    overflow: hidden;

    & > img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }

  &_quote {
    margin-top: 20px;
    color: white;
    width: 30px;
  }

  &_info {
    color: white;
    display: flex;
    width: 80%;
    justify-content: space-between;

    &-item {
      & > span {
        &:first-child {
          font-size: 40px;
          font-weight: bold;
        }
        display: block;
      }
    }
  }
}
