.timeline {
  position: relative;
  overflow: hidden;
  padding-bottom: 50px;
  margin: 0 auto;
  width: 970px;

   @media only screen and (max-width: 1830px) {
        padding: 0;
      }

  &::before {
    position: absolute;
    content: "";
    left: 49.9%;
    height: 100vh;
    z-index: -1;
    width: 1px;
    background: #151d2a;
    display: table;
  }

  &_title {
    text-align: center;
    text-transform: uppercase;
    font-size: 14px;
    display: block;
    color: #fff;
    margin-top: 6px;

    & > strong {
      background: #151d2a;
      border: 2px solid #fff;
      padding: 8px 10px;
    }
  }

  &_group {
    position: relative;
    margin-bottom: 20px;
    margin-top: 100px;

    &::after {
      clear: both;
    }

    &::before {
      display: table;
      content: " ";
    }

    &-1 {
      & > .date {
        min-width: 160px;
        text-align: center;
        padding: 8px 16px;
        float: left;
        left: 29%;
        background: #151d2a;
        color: #fff;
        font-weight: 700;
        position: relative;
        text-transform: uppercase;
        z-index: 1;

        &::before {
          position: absolute;
          content: "";
          transform: rotate(45deg);
          width: 12px;
          height: 12px;
          background: #151d2a;
          z-index: -1;
          top: 13px;
          right: -6px;
        }
      }

      & > .circle {
        width: 16px;
        height: 16px;
        background: #fff;
        display: block;
        position: absolute;
        margin: 0 auto;
        left: -2px;
        right: 0;
        top: 11px;
        border-radius: 100%;
        border: 4px solid #151d2a;
      }

      & > .description {
        border: 1px solid #151d2a;
        float: right;
        background: #fff;
        width: 45%;
        padding: 10px 25px 10px 25px;
        position: relative;
        transition: 0.5s ease-in-out;
        color: #151d2a;
      }
    }

    &-0 {
       
      & > .date {
        min-width: 160px;
        text-align: center;
        padding: 8px 16px;
        float: right;
        right: 29%;
        background: #151d2a;
        color: #fff;
        font-weight: 700;
        position: relative;
        text-transform: uppercase;

        &::before {
          position: absolute;
          content: "";
          transform: rotate(45deg);
          width: 12px;
          height: 12px;
          background: #151d2a;
          z-index: -1;
          top: 13px;
          left: -6px;
        }
      }

      & > .circle {
        width: 16px;
        height: 16px;
        background: #fff;
        display: block;
        position: absolute;
        margin: 0 auto;
        left: -2px;
        right: 0;
        top: 11px;
        border-radius: 100%;
        border: 4px solid #151d2a;
      }

      & > .description {
        border: 1px solid #151d2a;
        float: left;
        background: #fff;
        width: 45%;
        padding: 10px 25px;
        position: relative;
        transition: 0.5s ease-in-out;
        color: #151d2a;

        &::before {
            position: absolute;
            content: "";
            transform: rotate(45deg);
            width: 12px;
            height: 12px;
            background: #151d2a;
            z-index: -1;
            top: 13px;
            right: -6px;
          }
      }
    }
  }
}
