.about {
  padding: 0 375px;

  @media only screen and (max-width: 1475px) {
    padding: 0 200px;
  }

  @media only screen and (max-width: 958px) {
    padding: 0 20px;
  }

  &-infomation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 0px;
    border-bottom: 2px #eee solid;

    &-des {
      border-left: 2px #eee solid;
      padding-left: 20px;

      & > div {
        text-align: left;

        &:first-child {
          font-weight: 600;
        }
      }
    }
  }

  &-me {
    text-align: left;
    display: flex;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 50px;

    & > span {
      font-size: 30px;
      font-weight: 900;
      padding: 0 10px;
    }

    & > h1 {
      line-height: 50px;
      border-bottom: 1px gray solid;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        width: 54px;
        height: 2px;
        background: #151d2a;
        bottom: -1px;
        right: 0;
      }
    }

    & > p {
      font-size: 20px;
      font-weight: 500;
    }
  }

  &-intro {
    display: flex;
    &_avatar {
      width: 200px;
      height: 200px;
      object-fit: cover;
    }

    & > p {
      text-align: left;
      padding: 0 30px;
    }
  }
}
